@charset "UTF-8";
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

* {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-size: 18px;
  font-family: "PT Sans", "Arial", sans-serif;
  line-height: 26px;
  background-color: #f2f2f2;
}

.page__body {
  max-width: 1200px;
  min-height: 100%;
  margin: 0 auto;
  background-color: #ffffff;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  padding: 15px 30px 13px;
  color: #ffffff;
  background-color: #81b3d2;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  border: none;
  cursor: pointer;
}

.main-header {
  display: flex;
  flex-direction: column;
}

.main-header__logo {
  position: absolute;
  left: calc(50% - 92.5px);
  height: 96px;
  z-index: 2;
}

.main-nav {
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  position: relative;
  width: auto;
  min-height: 56px;
  background-color: #ffffff;
}

.main-nav--opened {
  z-index: 3;
}

.main-nav--opened .main-nav__list {
  position: absolute;
  width: 100%;
}

.main-nav__toggle {
  position: absolute;
  display: block;
  top: 0;
  right: 0;
  width: 68px;
  height: 56px;
  background: none;
  z-index: 2;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  left: 25px;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
}

.main-nav--closed .main-nav__toggle:active::before {
  background-color: fade(#81b3d2, 30%);
  box-shadow: 0 8px 0 0 fade(#81b3d2, 30%), 0 16px 0 0 fade(#81b3d2, 30%);
}

.main-nav--opened .main-nav__toggle {
  top: 0;
  right: 0;
}
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 25px;
  right: 18px;
  width: 26px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before, .main-nav--opened .main-nav__toggle:active::after {
  background-color: #ffffff;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .main-nav__list {
    grid-template-columns: repeat(5, 1fr);
    background-color: #ffffff;
  }
}

.main-nav--closed .main-nav__list {
  display: none;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__list {
    display: grid;
  }
}

.main-nav--nojs {
  z-index: 3;
}

.main-nav--nojs .main-nav__list {
  display: block;
  z-index: 3;
}
@media (min-width: 768px) {
  .main-nav--nojs .main-nav__list {
    display: grid;
  }
}

.main-nav__item {
  color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav__item {
    color: #000000;
  }
}

@media (min-width: 768px) {
  .main-nav__item:nth-child(4n-1) {
    grid-column: 4/5;
  }
}

.main-nav__item--last {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .main-nav__item--last {
    background: none;
  }
}

.main-nav__link {
  display: block;
  padding: 17px 0 12px;
  text-align: center;
  text-decoration: none;
  font-size: 18px;
  line-height: 26px;
  color: #ffffff;
  border-bottom: 1px solid rgba(1, 1, 1, 0.2);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .main-nav__link {
    border: none;
    font-size: 14px;
    line-height: 21px;
    color: #000000;
  }
}

@media (min-width: 768px) {
  .main-nav__link--active {
    color: #766357;
  }
}

