  text-transform: uppercase;
}
@media (min-width: 768px) {
  .form__warning {
    align-self: center;
    color: #000000;
  }
}

.form-item {
  display: flex;
  cursor: pointer;
  align-items: center;
}

.form-item__mark {
  position: relative;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 4px;
}

.form-item__label {
  margin-left: 16px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: none;
}

.form-item:hover .form-item__mark {
  background-color: #e5e5e5;
}

.form-item:focus-within .form-item__mark {
  background-color: #e5e5e5;
  outline: 2px solid #5496bd;
}

.form-item:active .form-item__mark {
  background-color: #d5d5d5;
}

.form-item__input:disabled ~ .form-item__mark {
  opacity: 0.6;
  cursor: not-allowed;
}

.form-item__input[type=checkbox]:checked + .form-item__mark {
  background-image: url("/img/check.svg");
  background-repeat: no-repeat;
  background-position: center center;
}

/* радиокнопки */
.form-item__input[type=radio] + .form-item__mark {
  border-radius: 50%;
}

.form-item__input[type=radio]:checked + .form-item__mark::before {
  position: absolute;
  top: 15px;
  left: 15px;
  width: 16px;
  height: 16px;
  background-color: #333333;
  border-radius: 50%;
  content: "";
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 2;
}

.modal--close {
